.Post {
    width: 100%;
    padding: 16px;
    text-align: left;
    /* border: 1px solid #eee;
    box-shadow: 2px 2px 4px #ccc; */
    margin: 10px;
    /* box-sizing: border-box; */
    cursor: pointer;
    align-items: center;
    /* border-radius: 25px; */
    border-bottom: solid grey 0.5px;
    margin-top: 0px;
    padding-top: 0px;
    
}

article h1 {
    margin-top: 0;
    
}

/* .Post:hover,
.Post:active {
    background-color: #98C1D9;
    
} */

.Type {
    margin: 16px 0;
    color: #ccc;
}

button {
    /* margin:0 .5rem ; */
    border: none;
    background-color: #1A8FE3;
    border-radius: 3px;
    min-width: 50px;
    width: 6rem;
    height:1.75rem;
    color:white;
    margin-top: 0.5rem;
    margin-right: 2.25rem;
    
}

.Answer-Section {
    display: flex;
    flex-direction: column;
    /* border: solid orange; */
}



.Question-Answer {
    width: 90%;
    height: 2rem;
    max-height: 8rem;
    /* border: solid green; */
    align-self: center;
    font-family: 'Open Sans', sans-serif;
} 




.Answer-Button {
    display:      block;
    margin-left:  auto;
    margin-right: 2.2rem;
    list-style: none;
    margin-top: 1rem;
 
}

.Answer-Button input {
    background-color: #1A8FE3;
    border-style: none;
    height: 2rem;
    width: 5.5rem;
    border-radius: 5px;
    color: white;
    
}

.Info ul {
    /* border: solid orange; */
    width: 95%;
}

.Body-Content {
    color: #8a817c;
}

.Answer {
    align-items: center;
    /* border: solid red;  */
    margin: auto;
    margin-left: 2.5rem;
    max-height: 15rem;
    overflow: scroll;
    
}
